<template>
  <div class="swiper-container mySwiper" :class="info.name">
    <div class="swiper-wrapper">
      <slot></slot>
    </div>
  </div>
</template>
<script>
import Swiper from "swiper/bundle";
import "swiper/swiper.min.css";
export default {
  props: {
    info:{
      type:Object,
      dafault:()=>{
        return {
          default:4,
          name:'mySwiper'
        }
      }
    }
  },
  mounted() {
    new Swiper("." + this.info.name, {
      slidesPerView: this.info.preview,
      spaceBetween: 10,
      freeMode: true
    });

    // new Swiper(".mySwiper", {
    //   slidesPerView: 4,
    //   spaceBetween: 10,
    //   freeMode: true
    // });

  },
};
</script>