<template>
  <div v-if="dataList">
    <Detail-heater>{{dataList.name}}</Detail-heater>
    <Img-detail :data-list="dataList"></Img-detail>
    <Film-info :data-list="dataList"></Film-info>
    <Actors :data-list="dataList"></Actors>
    <Photos :data-list="dataList"></Photos>
    <Buy v-if="dataList.isSale" :dataList="dataList"></Buy>
  </div>
</template>
<script>
import DetailHeater from "@/components/details/DetailHeater";
import ImgDetail from "@/components/details/ImgDetail";
import FilmInfo from "@/components/details/filmInfo";
import Actors from "@/components/details/actors";
import Photos from "@/components/details/photos";
import http from "@/util/http.js";
// import footerShow from '@/util/footerShow.js'
import Buy from '@/components/details/buy'
import { Dialog } from 'vant';

export default {
    // mixins: [footerShow],
    data(){
        return {
            dataList:null
        }
    },
    mounted() {
      
      
      // console.log("" + "id:" + this.$route);
      http({
        url: `/gateway?filmId=${this.$route.params.myID}&k=${this.$store.state.cityId}`,
        headers: { "X-Host": "mall.film-ticket.film.info" },
      })
      .then(res => {
        
        this.dataList = res.data.data.film;
        
        this.$nextTick(()=>{
          if(!this.dataList.isSale){
        Dialog.confirm({
            width:18.2708+'rem',
            overlayStyle:{},
            message:
              '',
              cancelButtonText:"",
              confirmButtonText:"",
              confirmButtonColor:"green",
              className:'dialog'
          })
        .then(() => {
          
          this.$router.push({
            path:'/films'
          })
        })
        .catch(() => {

        });
      }
        })
      });
    },
  components: {
    ImgDetail,
    FilmInfo,
    Actors,
    Photos,
    DetailHeater,
    Buy
  },
};
</script>
<style lang="scss">
.dialog{
  // padding: 1.125rem 1.25rem;
  // box-sizing: border-box;
  border-radius: 2px;
}
.van-dialog__message{
  padding: 1.125rem 1.25rem;
  box-sizing: border-box;
  color: #191a1b!important;
    font-size: 15px!important;
        line-height: 36px;
}
</style>