<template>
  <div :class="{ [name]: true }" :style="{ height, overflow: 'hidden', position: 'relative' }">
    <!-- {{name}} -->
    <ul>
      <!-- storecinemasData -->
      <slot></slot>
      <div v-for="item in $store.state.cinemasData" :key="item.cinemaId">
        <li v-if="!isInclude || isInclude.cinemaList.includes(item.cinemaId)" @click="choiceCinema(item.cinemaId)">
          <div class="cinema-info-contianer">
            <span class="cinema-info">{{ item.name }}</span>
            <span class="address cinema-info">{{ item.address }}</span>
          </div>
          <div>
            <span style="color: green">{{ item.lowPrice / 100 }}</span>
            <span style="text-align: right" class="address"></span>
          </div>
        </li>
      </div>
    </ul>
  </div>
</template>
<script>


import BetterScroll from "better-scroll";


export default {
  props: {
    height: {
      type: String,
      default: '0px'
    },
    name: {
      type: String,
      default: 'box'
    },
    isInclude: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      filmName: '',
      width: document.documentElement.clientHeight
    };
  },

  mounted() {
    // actionsgetCinemasData  
    if (this.$store.state.cinemasData.length === 0) {
      // console.log(this.$store.dispatch('getCinemasData',this.$store.state.cityId));
      let p = this.$store.dispatch('getCinemasData', this.$store.state.cityId)
      p.then(() => {
        this.init()
      })
    } else {
      this.init()

    }

  },
  methods: {
    init() {
      this.$nextTick(() => {
        const bs = new BetterScroll("." + this.name, {
          scrollbar: {
            fade: true,
          },
          mouseWheel: true,
          click: true
        })
      })
    },
    choiceCinema(cinemaId) {
      this.$router.push({
        name: '',
        params: {
          cinemaId
        }
      })
    },
    clickCity() {
      this.$router.push({
        path: '/city'
      });
    },
    searchCinema() {
      this.$router.push({
        path: '/cinemas/search'
      })
    }
  }
};
</script>
<style lang="scss" scoped>
ul {
  background-color: #fff;

}
ul>div:last-child {
  border-bottom:0;
  margin-bottom: 3.0625rem;
  li{
    border-bottom: none;
  }
}

li {
  font-size: .9375rem;
  color: #191a1b;
  height: 4.7188rem;
  width: 24.375rem;
  box-sizing: border-box;
  padding: 0.9375rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ededed;
  span {
    display: block;
  }

  .cinema-info-contianer {
    width: 80%;
    padding-right: 15px;
    box-sizing: border-box;
  }

  .cinema-info {
    // max-width: 80%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.address {
  color: #797d82;
  font-size: 0.75rem;
  margin-top: 0.3125rem;
}

.box {
  position: relative;
  overflow: hidden;
}
</style>
