const routes = [
  {
    path:'/schedule/:scheduleId',
    component:Schedule,
    name:'schedule'
  },
  {
    path:'/info',
    component:Info,
    meta:{
      isMain:true,
    }
  },
  {
    path:'/city',
    component:City
    
  },
  {
    ///filmsFilms
    path: '/films',
    component: Films,
    
    children: [
      {
        path: 'comingsoon',
        component: comingsoon,
        meta:{
          isMain:true,
        },
      },
      {
        path: 'nowPlaying',
        component: nowPlaying,
        meta:{
          isMain:true,
        },
      },
      {
        path: '/films',
        redirect: '/films/nowPlaying'
      }
    ]
  },
  {
    path: '/center',
    component: Center,
    meta:{
      isMain:true,
    },
    children:[
      {
        path:'/center/signOut',
        component:SignOut,
      },
      // {
      //   path:'/center',
      //   component:SignOut
      // }
    ]
  //   beforeEnter:(to,from,next)=>{             //
  //     if(localStorage.getItem('token')){        //
  //       next()
  //     }else{
  //     next({
  //       path:'/login',
  //       query:{redirect:to.fullPath}
  //     })
  //   }
  // }
  },
  {
    path: '/cinemas',
    meta:{
      isMain:true,
    },
    //  


                                      // component: ()=>
                                      //   import('@/views/Cinemas'),
    component:Cinemas,
  },
  {
    name:'',
    path:'/cinemas/:cinemaId/film',
    component:ChoiceCinema
  },
  {
    path:'/cinemas/search',
    component:Search
  },
  {
    path:'/Login',
    component:Login
  },
  {
    path:'/order',
    component:order,
    meta:{
      isNext:true     //
    }
  },
  {
    name:'',
    path: '/detail/:myID',  //  detail  route
    component: detail,
  },
  {
    name:'',
    path: '/detail/:myID/cinemas',
    component: FilmCinemas,
    props: true           //this.$route.paramsprops
  },
  {
    path: '*',// 
    redirect: "/center"
  }
]

const router = new VueRouter({
  mode:'hash',//hash   history  index     history# #  #
  routes
})

