const moment = require("moment")
const getTime = function (time, arr) {
    const computed = {}
    const weekDay = ["", "", "", "", "", "", ""]
    function getYmd(time) {
        let date = new Date(time)
        return [date.getFullYear(), date.getMonth(), date.getDate()]
    }

    let date = new Date(time);              //2023-04-05T16:00:00.000Z
    let now = new Date(Date.now())
    // console.log(time,now);
    computed.year = date.getFullYear() +    ""
    computed.month = date.getMonth() + 1 +  ""     //1 
    computed.day = date.getDate() +         ""     ////
    computed.hour =
        (date.getHours() < 10 ? "0" + date.getHours() : date.getHours()) + ":";
    computed.minute =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();

    computed.week = weekDay[moment(date).day()]         //

    date = getYmd(date);
    now = getYmd(now);
    for(let i=0;i<3;i++){
        if(date[i]===now[i]){
            if(i===1){
                if(date[2]===now[2]){
                    computed.week = ""
                }else if(date[2]===(now[2]+1)){
                    computed.week = ""
                }else if(date[2]===(now[2]+2))
                computed.week = ""
            }
        }else{
            break
        }
    }
    return arr.map(item => computed[item]).join("")
}
// console.log(getTime(1680796800000,["week", "month", "day", "hour", "minute"]));
// console.log(getTime(1681003800000, ["week", "month", "day", "hour", "minute"]));
module.exports = getTime