<template>
  <div>
    <ul class="filmsDetail" >
      <li
        v-for="item in dataList"
        :key="item.filmId"
        @click="handleChangePage(item.filmId)"
      >
        <img :src="item.poster" alt="" />
        <div class="FilmInformation">
          <div>
            <span class="name">{{ item.name }} </span
            ><span class="item">{{ item.filmType.name }}</span>
          </div>
          <div>
            <span class="label"
              ><span v-if="item.actors">{{ item.actors | actors }}</span
              ><span v-else></span></span
            >
          </div>
          <div class="label">
            {{(item.premiereAt*1000) | dateFilter}}
          </div>
        </div>
        <div class="buy" v-if="item.isPresale"></div>
      </li>
    </ul>
    <div v-if="show" class="noMore"></div>
  </div>
</template>
<script>
import moment from 'moment'
import http from "@/util/http.js";
import Vue from "vue";
Vue.filter("actors", (v) => {
  return v.map((item) => item.name).join(" ");
});
Vue.filter("dateFilter", (data) => {
  let time = moment(data).format();
  let d = new Date(time);
  let week = moment(data).day()
  let getWeet =(function(){
switch (week) {
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ''
        case 4:
          return ''
        case 5:
          return ''
        case 6:
          return ''
        case 0:
          return ''
      }
  })()
  return (
     getWeet+' '+ (d.getMonth() + 1) + "" + d.getDate() + ""
  );
});
export default {
  data() {
    return {
      dataList: [],
      page: 1,
      tag: true,
      show:false
    };
  },
  methods: {
    handleChangePage(id) {
      // 
      // location.href = '#/detail'     

      // this.$routermainrouter
      // 1.
      // this.$router.push(`/detail/${id}`)

      // 2.
      this.$store.commit('footerHide')
      this.$router.push({
        name: "", //
        params: {
          //path
          //es6     id
          myID: id,
        },
      });
    },
    sendRequest() {
      this.tag = false;
      http({
        // 
        
        url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.page++}&pageSize=10&type=2&k=2116`,
        headers: {
          "X-Host": "mall.film-ticket.film.list",
        },
      }).then((res) => {
        if (this.dataList.length!=res.data.data.total) {
          this.dataList.push(...res.data.data.films)
          this.tag = true;
          }else {
            this.show = true;
          }
      });
    },
  },
  mounted() {
    this.sendRequest();
    const html = document.documentElement;
    window.onscroll = ()=> {
      if (
        html.scrollHeight - html.clientHeight - 40 <= html.scrollTop &&
        this.tag
      ) {
        
        this.sendRequest();
      }
    };
  },
    beforeDestroy(){
      window.onscroll =null;
    }
};
</script>

<style lang="scss" scoped>
.filmsDetail li {
  background-color: #fff;
  height: 7.75rem;
  padding: .9375rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  img {
    // height: ;
    width: 4.125rem;
  }
  .FilmInformation {
    padding: 0 .625rem;
    box-sizing: border-box;
    width: 15.25rem;
    .name {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: inline-block;
      margin-right: 5px;
      color: #191a1b;
      max-width: 12.4375rem;
      vertical-align: middle;
    }
    .item {
      display: inline-block;
      font-size: 12px;
      color: #fff;
      background-color: #d2d6dc;
      height: 14px;
      line-height: 14px;
      padding: 0 2px;
      border-radius: 2px;
      vertical-align: middle;
    }
    div {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 14rem;
    }
  }
  .label {
    font-size: .8125rem;
    color: #797d82;
  }
}
.buy {
  flex: 1;
  height: 1.5625rem;
  border: 1px solid rgb(0,188,0);
  line-height: 1.5625rem;
  color: rgb(0,188,0);
  font-size: 13px;
  text-align: center;
  border-radius: 2px;
}
.noMore{
  height: 3.6875rem;
    background-color: #ededed;
    color: #bdc0c5;
    font-size: .8125rem;
    text-align: center;
    margin: auto;
    line-height: 3.6875rem;

}
</style>
