<template>
    <div class="main">
        <header v-if="cinemaInfo">
            <div class="header-left"><van-icon class="return" name="arrow-left" size='24' @click='$router.back()'/></div>
            <div data-v-18fbab6c="" class="header-title">{{cinemaInfo.name}}</div>
            <div class="services" v-if="cinemaInfo.services">
                <span class="services-item" v-for="(item,index) in cinemaInfo.services" :key="index">{{item.name}}</span>
                <van-icon name="arrow" size="12"/>
            </div>
            <div class="address"><van-icon name="location-o" size='24'/>
                <div class="address-dev">
                {{cinemaInfo.address}}
                </div>
                <a :href="`tel:${cinemaInfo.phone}`"><van-icon name="phone-o" size='24'/></a>
            </div>
        </header>
        <section  v-if="filmsInfo.length">
            <div class="film">
                <div class="bg" :style="{backgroundImage:`url(${filmsInfo[currentIndex].poster})`}"></div>
            <div class="mySwiper">
                <CinemaSwiper  @setIndex = 'current' :index = 'currentIndex'>
                    <CinemaSwiperItem v-for="(item,index) in filmsInfo" :key='index'>
                        <img class="swiperItem" :src="item.poster" alt="">
                    </CinemaSwiperItem>
                </CinemaSwiper>
            </div>
            </div>
            <div class="film-info" @click="$router.push(`/detail/${filmsInfo[currentIndex].filmId}`)">
                <div class="film-head"><span class="film-name">{{filmsInfo[currentIndex].name}}</span><span class="film-score">{{filmsInfo[currentIndex].grade}}</span><span class="film-unit"></span></div>
                <div class="film-dec">{{filmsInfo[currentIndex].category}} | {{filmsInfo[currentIndex].runtime}} | {{filmsInfo[currentIndex].director+' | '}}{{filterActors(filmsInfo[currentIndex].actors)}}
                </div>
                <img data-v-18fbab6c="" src="data:image/png;base64," width="4px" height="8px" alt="" class="film-more">
            </div>
            <keep-alive>
            <FilmList v-if="cinemaInfo" :filmsInfo="filmsInfo" :currentIndex='currentIndex' :cinemaInfo='cinemaInfo' :key='currentIndex'></FilmList>
            </keep-alive>
        </section>
    </div>
</template>
<script>

import http from '@/util/http.js'
import CinemaSwiper from '@/components/CinemaSwiper'
import CinemaSwiperItem from '@/components/CinemaSwiperItem'
import FilmList from '@/components/FilmList'
import {Icon} from "vant"
export default {
    data(){
        return {
            cinemaInfo:null,
            filmsInfo:[],
            currentIndex:0,
            window:window
        }
    },
    components:{
        [Icon.name]:Icon,
        CinemaSwiper,
        CinemaSwiperItem,
        FilmList
    },
    methods:{
        
        filterActors(arr){
            
            return arr.map(v=>{
                return v.name
            }).join(' ')
        },

        current(index){
            this.currentIndex = index
        },
        setSectionHeight(){
            let header = document.querySelector("header")
            document.querySelector(".main").style.setProperty("--HeaderHeight",header
            .clientHeight+"px")
        }
    },
    mounted(){
        Promise.all([
        http({
            url:`https://m.maizuo.com/gateway/?cinemaId=${this.$route.params.cinemaId}&k=4650178`,
            headers:{
                'X-Host': 'mall.film-ticket.cinema.info'
            }
        }).then(res=>{
            this.cinemaInfo = res.data.data.cinema;
        }),
        http({
            url:`https://m.maizuo.com/gateway/?cinemaId=${this.$route.params.cinemaId}&k=4650178`,
            headers:{
                'X-Host': 'mall.film-ticket.film.cinema-show-film'
            }
        }).then(res=>{
                this.filmsInfo = res.data.data.films
                if(this.$store.state.filmId)
            this.currentIndex = this.filmsInfo.findIndex(v=>v.filmId===this.$store.state.filmId)
        })]).then(()=>{
            this.setSectionHeight()
        })
    },
}
</script>
<style lang="scss" scoped>
.main{
    min-height: 100vh;
    background: #fff;
    overflow: auto;
    section{
    position: relative;
    overflow: auto;
    // height: var(--sectionHeight);
    height: calc(100vh - var(--HeaderHeight));
}
}
.schedule-item{
    height: 4.625rem;
    background: red;
}
div{
    box-sizing: border-box;
}


header{
    top: 0px;
    position: sticky;
    z-index: 99999;
    background-color: inherit;
    // height: ;
}
.header-left{
    width: 100%;
    height: 2.75rem;
    display: flex;
    align-items: center;
    padding-left: .9375rem;
}
.header-title{
    background: #fff;
    height: 2.75rem;
    line-height: 2.75rem;
    text-align: center;
    font-size: 1.0625rem;
    color: #191a1b;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.services{
    height: 2.375rem;
    color: #ffb232;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    padding: .3125rem 0 .9375rem 0;
}
.services-item{
    position: relative;
    padding: 0 .375rem;
    margin: 0 .1563rem;
    font-size: .625rem;
    border: .0625rem solid #ffb232;

}
.address{
    display: flex;
    align-items: center;
    height: 3.125rem;
    color: #191a1b;
    padding-left: 1.0625rem;
    a{
        padding: 0 1.5625rem;
        color: inherit;
    }
}
.address-dev{
    padding-left: .75rem;
    flex: 1;
    font-size: .875rem;
    height: 1.25rem;
    padding: 0 .75rem;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.film{
    height: 10rem;
    position: relative;
    overflow: hidden;
    padding: .9375rem 0;
}
.bg{
    width: 100%;
    position:absolute;
    height: 8.125rem;
    filter: blur(1.875rem);
    -webkit-filter: blur(1.875rem);
}
.swiperItem{
    height: 8.125rem;
    width: 5.625rem;
}
.film-info{
    position: relative;
    text-align: center;
    height: 5rem;
    padding: .9375rem 0;
}
.film-info::after{
    content: '';
    display: block;
    height: 0;
    width: 0;
    border: .625rem solid transparent;
    border-bottom: .625rem solid #fff;
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -0.625rem;
}
.film-head{
    margin-bottom: .625rem;
}
.film-name{
    font-size: .9375rem;
    color: #191a1b;
    padding-right: .3125rem;
}
.film-score{
        font-size: 1rem;
    font-style: italic;
    color: #ffb232;
}
.film-unit{
        font-size: .625rem;
    color: #ffb232;
}
.film-dec{
    text-align: center;
    height: 1.125rem;
    color: #797d82;
    font-size: .8125rem;
    padding: 0 12%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.film-more {
    position: absolute;
    right: .9375rem;
    top: 0;
    margin-top: 2.25rem;
}
</style>