<template>
    <div>
        <img src="data:image/png;base64," alt="" @click="$router.back()">
        <div class="title"  v-back>
            <!-- 11111111 -->
            <slot></slot>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
Vue.directive('back',{
    inserted(el){
        window.onscroll =()=>{
            if(window.pageYOffset>30){
                el.style.height = el.scrollHeight+'px'
            }else{
                el.style.height = 0+'px'
            }
        }
    },
    unbind(){
        window.onscroll = null
        
    }
})
export default {

}
</script>
<style scoped>
.title{
    position: fixed;
    height: 0px;
    width: 24.375rem;
    transition: .3s all;
    line-height: 2.75rem;
    text-align: center;
    font-size: 1.0625rem;
    background-color: #fff;
    overflow: hidden;
}
img{
    height: 1.875rem;
    position:fixed;
    top:.3125rem ;
    left: .3125rem;
    z-index: 999;
    
}
</style>